import { Component, OnInit } from "@angular/core";

import { SourceService } from "../services/source.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Source } from "app/shared/models/source.model";

@Component({
  selector: "app-edit-source",
  templateUrl: "./edit-source.component.html",
  styleUrls: ["./edit-source.component.scss"]
})
export class EditSourceComponent implements OnInit {
  updatedsource: Source;
  sourceid;

  constructor(
    public sourceservice: SourceService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedsource = new Source({});

    this.sourceid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.sourceservice.getSourceById(this.sourceid).subscribe(
      (data: any) => {
        console.log("sourcelist>>", data);
        this.updatedsource = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedsource);

    swal
      .fire({
        title: "Do you want to edit the source details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.sourceservice.editSource(this.updatedsource).subscribe(
            (data: any) => {
              swal
                .fire("Edit!", "Source User has been edited.", "success")
                .then(() => {
                  this.router.navigate(["account/source"]);
                });
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
