import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { FormsModule } from "@angular/forms";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { AddSourceComponent } from "../source/add-source/add-source.component";
import { RouterModule, Routes } from "@angular/router";
import { SourceComponent } from "./source.component";
import { SourceService } from "./services/source.service";
import { EditSourceComponent } from "./edit-source/edit-source.component";
import { SourceRoutingModule } from "./source-routing.module";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    NgbModule,
    SourceRoutingModule,
    RouterModule
  ],

  declarations: [SourceComponent, AddSourceComponent, EditSourceComponent],

  providers: [SourceService]
})
export class SourceModule {}
