import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { SourceService } from "./services/source.service";
import swal from "sweetalert2";
import { Source } from "../../shared/models/source.model";

@Component({
  selector: "app-source",
  templateUrl: "./source.component.html",
  styleUrls: ["./source.component.scss"]
})
export class SourceComponent implements OnInit {
  sourceList: Array<Source>;

  constructor(public sourceservice: SourceService, private router: Router) {
    this.sourceList = [];
  }

  ngOnInit() {
    this.sourceservice.getSource().subscribe(
      (data: any) => {
        console.log("sourcelist>>", data);
        this.sourceList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("source id>>", id);
    this.router.navigate(["account/manage/source/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the source ?",
        text: "You won't be able to revert the deleted source",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.sourceservice.deleteSource(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Source has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
