import { Component, OnInit } from "@angular/core";
import { SourceService } from "../services/source.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { Source } from "app/shared/models/source.model";

@Component({
  selector: "app-add-source",
  templateUrl: "./add-source.component.html",
  styleUrls: ["./add-source.component.scss"]
})
export class AddSourceComponent implements OnInit {
  newsource: Source;
  submitting: boolean;

  constructor(
    public sourceservice: SourceService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newsource = new Source({});
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newsource);

    swal
      .fire({
        title: "Do you want to add the source ?",
        text: "New source will be added in Source List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.sourceservice.addSource(this.newsource).subscribe(
            (data: any) => {
              swal
                .fire("Add!", "Source has been added.", "success")
                .then(() => {
                  this.router.navigate(["account/source"]);
                });
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
