import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { BaseService } from "./../../../shared/services/base.service";
import { Source } from "app/shared/models/source.model";

@Injectable()
export class SourceService extends BaseService {
  testurl;
  constructor(public http: HttpClient) {
    super("source");
  }

  getSource() {
    return this.http.get(this.url, this.getOptions());
  }

  getSourceById(id) {
    return this.http.get(this.url + id, this.getOptions());
  }

  editSource(data: Source) {
    return this.http.put(
      this.url + data._id,
      { data: data },
      this.getOptions()
    );
  }

  deleteSource(id) {
    return this.http.delete(this.url + id);
  }

  addSource(data: Source) {
    console.log("url>>", this.url);
    console.log("data>>", data);

    return this.http.post(this.url, { data: data }, this.getOptions());
  }
}
